class SideBarAdmin extends HTMLElement {
  constructor() {
    super();
  }

  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
      <nav
        id="sidebarMenu"
        class="mt-5 col-md-3 col-lg-2 d-md-block sidebar collapse"
        style="background-color:#3F4E4F"
      >
        <div class="position-sticky pt-3">
          <ul class="nav flex-column">
            <li class="nav-item mb-3">
              <a
                class="nav-link linkSidebar"
                id="dashboard-link"
                aria-current="page"
                href="#/dashboard"
              >
                <span data-feather="home"></span>
                Dashboard
              </a>
            </li>
            <li class="nav-item mb-3">
              <a class="nav-link linkSidebar" id="wisata-link" href="#/wisata">
                <span data-feather="file"></span>
                Wisata
              </a>
            </li>
            <li class="nav-item mb-3">
              <a class="nav-link linkSidebar" id="hotel-link" href="#/hotel">
                <span data-feather="file"></span>
                Hotel
              </a>
            </li>
            <li class="nav-item mb-3">
              <a class="nav-link linkSidebar" id="gallery-link" href="#/galleryadmin">
                <span data-feather="file"></span>
                Gallery
              </a>
            </li>
            <li class="nav-item mb-3">
              <a class="nav-link linkSidebar" id="profil-link" href="#/profil">
                <span data-feather="shopping-cart"></span>
                Profil
              </a>
            </li>
          </ul>
        </div>
      </nav>
    `;
  }
}

customElements.define("sidebar-element", SideBarAdmin);
